import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import PreLoader, {apishka, Api, ws} from './Api';
//import Select from 'react-select';
import Select from 'react-select';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import Gallery from 'react-grid-gallery';
import TimePickerWrapper from 'react-times';
import { SketchPicker } from 'react-color';
import 'react-times/css/material/default.css';
import 'react-times/css/classic/default.css';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';

const qs = require('query-string')
class GetOne extends Component {
	
	constructor(props){
		super(props);
		this.getData = this.getData.bind(this);
		this.getSelectData = this.getSelectData.bind(this);
		this.changeData = this.changeData.bind(this);
		this.getTypeheadData = this.getTypeheadData.bind(this);
		this.visibleCondition = this.visibleCondition.bind(this);
		this.getAutocomData = this.getAutocomData.bind(this);
		this.multiSelectValue = this.multiSelectValue.bind(this);
		this.getMultiSelectData = this.getMultiSelectData.bind(this);
		this.getMultiTypeHeadData = this.getMultiTypeHeadData.bind(this);
		this.callAPI = this.callAPI.bind(this);
		this.saveFile = this.saveFile.bind(this);
		this.delFile = this.delFile.bind(this);
		this.ifParse = this.ifParse.bind(this);
		this.imageSize = this.imageSize.bind(this);
		this.dateTimeSave = this.dateTimeSave.bind(this);
		this.sendSocket = this.sendSocket.bind(this);
		
		let path = null
		let inputs = null
		
		let nowdate = new Date()
		let search = null
		nowdate =  nowdate.getDate() + '.' + (nowdate.getMonth()+1) + "." + nowdate.getFullYear() + ' '+ nowdate.getHours() + ':' +  nowdate.getMinutes() + ':' + nowdate.getSeconds() 		
		if (this.props.compo) {
			path = this.props.path
			inputs = this.props.inputs
			search = this.props.search
		} else {
			search = this.props.location.search
			inputs = qs.parse(this.props.location.search)
			path = this.props.match.params.path
		}
		
		this.state = {
			inputs:inputs,
			data:{},
			config:[],
			loading:true,
			title:null,
			table:null,
			classname:null,
			path:path,
			acts:[],
			viewid:null,
			subscrible:null,
			socket:false,
			nowdate:nowdate.toString(),
			search:search
		}
	}	
	
	componentDidMount(){		
		this.getData();		
	}
	
	sendSocket(socket) {
		let idcol = this.state.config.filter((x) => x.col.toUpperCase() === 'ID' && !x.related)[0].title
		let ids = []
		this.state.data.forEach((x) =>  ids.push(x[idcol]))
		
		socket.send(JSON.stringify({"viewpath":this.state.path, "ids":ids}))
	}
	
	getData() {

		this.setState({loading:true},() => {
			let print = false
			if (this.props.match)
				if (this.props.match.url.search('/getone/') < 0 && this.props.match.url.search('/getoneprint/') >= 0) 
					print = true	
			apishka('post', {  
				inputs: this.state.inputs	
			}, {path:this.state.path}, '/schema/getone', (data) => {
				this.setState({
					data:data.data[0] || {},
					config:data.config, 
					table:data.table,
					title:data.title,
					loading:false,
					acts:data.acts,
					classname:data.classname,
					viewid:data.id,
					subscrible:data.subscrible,
					user:data.user,
					print:print					
				},() => {
						document.title = this.state.title
						if (Object.keys(this.state.data).length > 0) {
							/*select*/
							let config = this.state.config.filter((obj) => (obj.type === 'select' || obj.type === 'multiselect') && obj.visible && this.state.data[obj.title])
							config.forEach((obj) =>	this.getSelectData(obj.col))
							
							/*typehead*/
							config = this.state.config.filter((obj) => (obj.type === 'typehead' || obj.type === 'multitypehead')  && obj.visible && this.state.data[obj.title])
							config.forEach((obj)=>	this.getTypeheadData(obj,null,this.state.data[obj.title]) )
							
						}	

						if (Object.keys(this.state.data).length  === 0 && this.state.inputs.relation) {
							let rel = this.state.inputs.relation.split(',')
							rel.forEach((x) => {
								let conf = this.state.config.filter((obj) => obj.col === x)[0]
								if (conf)
									this.changeData('','',this.state.inputs[conf.col],conf)
							})

						}
						
						if (data.subscrible && !this.state.socket && !print) {
							let socket = new WebSocket(ws);
							socket.onopen = () => {
								this.setState({socket:true}, () => 
									this.sendSocket(socket)
								)
							};

							socket.onclose = (event) => {
							  if (event.wasClean) {
								console.log('clear closed');
							  } else {
								NotificationManager.error('failed');
							  }
							  NotificationManager.error('code: ' + event.code + ' reason: ' + event.reason);
							};

							socket.onmessage = (e) => {
							  let data = JSON.parse(e.data)	
							  if (!data.error) {
								  data.forEach((x) => NotificationManager.info(x.notificationtext))
								  this.getData()
								 
							  }	else {
								  NotificationManager.error(data.error)
							  }
							};

							socket.onerror = (error) => {
							  NotificationManager.error("error " + error.message);
							};
						}
						
						if (print)
							window.print()
					}
				)
			}, (err) => {
				this.setState({loading:false}, () => {
					if (err.response.data) {
						NotificationManager.error(err.response.data.message)
					} else {
						NotificationManager.error('unknown error!')
					}	
				}) 		
			})	
		})		
	}

	componentWillReceiveProps(n){
		if (n.match)
			if (this.state.path !== n.match.params.path) {
				this.setState({path:n.match.params.path},this.getData())
			}
		if (JSON.stringify(n.inputs) !== JSON.stringify(this.state.inputs))
			this.setState({inputs:n.inputs},this.getData())
		
		if (n.compo && n.path !== this.state.path)
			this.setState({path:n.path, search:n.search},this.getData())
	}
	
	getSelectData(col) {
		let config = this.state.config
		config = config.filter((obj) => obj.col === col)
		let inputs = {}
		if (!config[0].selectdata){
			if (config[0].select_condition) {
				config[0].select_condition.forEach((obj) => {
					let value = null
					if (obj.value) {
						if (this.state.data[obj.value.value]) {
							value = this.state.data[obj.value.value]
							inputs[obj.value.value] = value
						} 	
					} else {
						inputs[obj.col.value] = obj.const
					}
					
				} )	
			}
			
			this.setState({loading:true},()=>{
				apishka('post', {  
					inputs: inputs,
					config:config[0]		
				}, {}, '/api/select', (data)=>{
					let newconfig = this.state.config
					let i = 0
					let dat = data.outjson
					dat.unshift({label:null,value:null})
					while (i < newconfig.length) {
						if (newconfig[i].col === col) {
							newconfig[i]["selectdata"] = dat
						}
						i += 1
					}
					this.setState({
						config:newconfig, 
						loading:false
					})
				}, (err)=>{
					if (err.response.data) {
						NotificationManager.error(err.response.data.message)
					}
					else {
						NotificationManager.error('unknown error!')
					}				
				})	
			})
		}

	}
	
	getAutocomData(item,val) {
		if (val && val.length>2) {
			apishka('post', {  
				val: val,
				table:this.state.table,
				col : item.col
			}, {}, '/api/autocomplete', (data)=>{
				let newconfig = this.state.config
				let i = 0
				while (i < newconfig.length) {
					if (newconfig[i].col === item.col) {
						newconfig[i]["selectdata"] = data.outjson
					}
					i += 1
				}
				this.setState({
					config:newconfig, 
					loading:false
				})
			}, (err)=>{
				if (err.response && err.response.data) {
					NotificationManager.error(err.response.data.message)
				}
				else {
					NotificationManager.error('unknown error!')
				}				
			})
		}
	
	}
	
	getTypeheadData(item,e, id, ismulti = null) {
		let val = e
		let config = this.state.config
		let col = item.col
		let title = item.title
		config = config.filter((obj) => obj.col === col && obj.title === title)
		let inputs = {}
		if ((val && val.length>1) || id) {
			if (config[0].select_condition) {
				config[0].select_condition.forEach((obj) => {
					let value = null
					if (obj.value) {
						if (this.state.data[obj.value.value]) {
							value = this.state.data[obj.value.value]
							inputs[obj.value.value] = value
						} 	
					} else {
						inputs[obj.col.value] = obj.const
					}	
				})	
			}

			//console.log('TYPE',item.type)
			if (item.type === 'multitypehead' && ismulti === null ) {
				ismulti = true
				e = id
				id = null
				
			}
				
			this.setState({loading:true},()=>{
				apishka('post', {  
					inputs: inputs,
					config:config[0],
					val : val, 
					id:id,
					ismulti:ismulti					
				}, {}, '/api/select', (data) => {
					let newconfig = this.state.config
					let i = 0
					while (i < newconfig.length) {
						if (newconfig[i].col === col) {
							newconfig[i]["selectdata"] = data.outjson
						}
						i += 1
					}
					this.setState({
						config:newconfig, 
						loading:false
					})
				}, (err)=>{
					if (err.response && err.response.data) {
						NotificationManager.error(err.response.data.message)
					}
					else {
						NotificationManager.error('unknown error!')
					}				
				})	
			})
		}
	}
	
	saveFile(e, conf){
		if (e.target.files.length>0) {
			let filedata = new FormData()
			
			for (let i in e.target.files) {
				if (this.state.data[conf.title]) {
					
					if (this.state.data[ conf.title ].filter((x) => x.filename ===  e.target.files[i].name).length === 0)
						filedata.append('file_'+i, e.target.files[i])
						
				} else {
					filedata.append('file_'+i, e.target.files[i])
				}
			}
			
			if (!filedata.has("file_0")){
				NotificationManager.error('File with the same title was uploaded before!')
				return	
			}

			filedata.append('config',JSON.stringify(conf))
			let id_title = this.state.config.filter((obj) => obj.col.toUpperCase() === "ID" && !obj.fn && !obj.relatecolumn )[0].title
			
			if (this.state.data[ id_title ])
				filedata.append('id',this.state.data[ id_title ])
			
			if (this.state.data[conf.title])
				filedata.append('value',JSON.stringify( this.state.data[ conf.title ]))	

			if (!conf.related) {
				filedata.append('tablename',this.state.table)
			} else {
				filedata.append('tablename',conf.table)			
				filedata.append('relatetable',this.state.table)			
			}
			
			filedata.append('viewid',this.state.viewid)
			
			this.setState({loading:true},()=>{
				
				apishka('post', filedata, {}, '/api/savefile', (data)=>{
					let dat = this.state.data
					data = data.outjson	
					if ( data.id ) {	
						dat[id_title] = data.id
						dat[conf.title] = JSON.parse(data.value)
					} else {
						dat = data
					}
					this.setState({
						data:dat,
						loading:false
					})
				}, (err)=>{
					this.setState({loading:false}, () => {
						if (err.response && err.response.data) {
							NotificationManager.error(err.response.data.message)
						}
						else {
							NotificationManager.error('unknown error!')
						}	
					})	
				})	
			})	
		}
	}
	
	delFile(conf, uri) {
		let val = this.state.data[conf.title]
		if (val) {
			val = val
			val = val.filter((x) => x.uri !== uri)
		}
		
		this.changeData(conf.col,conf.title,val, conf)
	}

	imageSize(conf,uri,type,val) {
		let title = conf.title
		let newdata = this.ifParse(this.state.data[title])
		let obj = newdata.filter((x) => x.uri === uri)[0]
		newdata = newdata.filter((x) => x.uri !== uri)
		obj[type] = val
		if (type === "width") {
			
			obj.thumbnailWidth = parseInt(val)
			//obj.sizes =  JSON.stringify({"width": val})
		}

		if (type === "height") {
			
			obj.thumbnailHeight = parseInt(val)		
		}
			
		
		newdata.push(obj)
		this.changeData(conf.col, conf.title, newdata, conf)
			
	}
	
	changeData(col,title,value, conf) {
		const od = Object.keys(this.state.data).length
		if (conf.type === "multiselect" || conf.type === "multitypehead") {
			//`let data = this.state.data

			let val = []
			value.forEach((x) => val.push(x.value))
			value = val

		}
		if (!conf.related) {
			let id_title = this.state.config.filter((obj) => obj.col.toUpperCase() === "ID" && !obj.fn && !obj.relatecolumn )[0].title
			this.setState({loading:true},()=>{
				apishka('post', {  
					tablename: this.state.table,
					config:conf,
					id:this.state.data[ id_title ],
					value:value,
					viewid:this.state.viewid	
				}, {}, '/api/saverow', (data)=>{
					data = data.outjson
					let dat = this.state.data
					if ((!this.state.data[id_title] & !data) || conf.updatable){
						let inputs = this.state.inputs
						if (this.state.data[id_title])
							inputs[id_title] = this.state.data[id_title]
						this.setState({inputs:inputs},
							this.getData())

					} else {
						if (data.id ) {	
							//console.log(data, dat, id_title, conf.title)
							dat[id_title] = data.id
							dat[conf.title] = value
						} else {
							dat = data
						}
						if (od === 0) {
							let cnf = this.state.config.filter((obj) => obj.defaultval )
							cnf.forEach((obj) => this.changeData(obj.col,obj.title, obj.defaultval, obj))
							this.setState({
								loading:false
							})
						}

						else {
							this.setState({
								data:dat, 
								loading:false
							})
						}
					}
				}, (err)=>{
					this.setState({loading:false}, () => {
						if (err.response.data) {
							NotificationManager.error(err.response.data.message)
						}
						else {
							NotificationManager.error('unknown error!')
						}	
					})	
				})	
			})
		} else {
			let id_title = this.state.config.filter((obj) => obj.col.toUpperCase() === "ID" && !obj.fn && !obj.relatecolumn )[0].title
			this.setState({loading:true},()=>{
				apishka('post', {  
					tablename: conf.table,
					config:conf,
					id:this.state.data[ id_title ],
					relatetable:this.state.table,
					value:value,
					viewid:this.state.viewid					
				}, {}, '/api/saverow', (data)=>{
					if (od === 0) {
						let cnf = this.state.config.filter((obj) => obj.defaultval )
						cnf.forEach((obj) => this.changeData(obj.col,obj.title, obj.defaultval, obj))
					} else 	if (conf.updatable) {
						let inputs = this.state.inputs
						if (this.state.data[id_title])
							inputs[id_title] = this.state.data[id_title]
						this.setState({inputs:inputs},
							this.getData())						
					}
					this.setState({
						loading:false
					})
				}, (err)=>{
					this.setState({loading:false}, () => {
						if (err.response.data) {
							NotificationManager.error(err.response.data.message)
						}
						else {
							NotificationManager.error('unknown error!')
						}	
					})	
				})	
			})
		}
	}
	
	visibleCondition(data,visible_condition) {
		
		if (!visible_condition || visible_condition.length === 0 ) {
			return true;
		} else {
			let a = true

			visible_condition.forEach((item) => {
				if (item.operation.js === ">") {
					if (data[item.col.value] && data[item.col.value].toString()>item.value) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "===") {
					if (data[item.col.value] && data[item.col.value].toString()===item.value) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "<") {
					if (data[item.col.value] && data[item.col.value].toString()<item.value) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "!==") {
					if (data[item.col.value] && data[item.col.value].toString() !== item.value) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === ">=") {
					if (data[item.col.value] && data[item.col.value].toString() >= item.value) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "<=") {
					if (data[item.col.value] && data[item.col.value].toString() <= item.value) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "===null") {
					if (data[item.col.value] === null ) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "!==null") {
					if (data[item.col.value] !== null ) {
						a = true && a	
					} else {
						a = false
					}	
				} else if (item.operation.js === "indexOf") {
					if (data[item.col.value].indexOf(item.value)!==-1 ) {
						a = true && a	
					} else {
						a = false
					}	
				}
			})
			return a
		}
	}
	
	queryBuilder2(itm) {
		let squery = ''
		if (itm.act.indexOf('?') === -1)
			squery = '?'
		if (itm.parametrs) {
			itm.parametrs.forEach((obj) => {
				if (obj.paramcolumn) {
					squery += obj.paramtitle + "=" + (this.state.data[this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0].title] || 
													  this.state.inputs[this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0].title]) + '&'
					 
				}
				else {
					squery += obj.paramtitle + "=" + obj.paramconst + '&'
				}
			}) 
		}
		
		squery = squery.substring(0,squery.length-1)
		return squery
	}
	
	multiSelectValue(val, title) {
		let ar2 = []	
		let select_data = this.state.config.filter((x) => x.title === title)[0].selectdata 
		
		if (select_data && val)
			val.forEach((x) => {
				if (select_data.filter((y) => y.value === x)[0]) {
					ar2.push({value:x,label:select_data.filter((y) => y.value === x)[0].label}) 
				}
			})
		return ar2
	}
	
	getMultiSelectData(conf, val) {
		if (conf.multiselecttable) {
			this.getSelectData(conf.col)
		} else {
			this.getAutocomData(conf, val)
		}
	}
	
	getMultiTypeHeadData(conf, val) {
		if (conf.multiselecttable) {
			this.getTypeheadData(conf,val, null,  false)
		} else {
			this.getAutocomData(conf, val)
		}
		

	}
	
	callAPI(item) {
		let body = {}
		if (item.parametrs) {
			item.parametrs.forEach((obj) => {
				if (obj.paramcolumn) {
					body[obj.paramtitle] = this.state.data[this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0].title] 
					 
				}
				else {
					body[obj.paramtitle] = obj.paramconst
				}
			}) 
		}
			this.setState({loading:true},()=>{
				apishka(item.actapitype, body, {}, item.act, (data)=>{
					this.setState({loading:false},() => this.getData())
				}, (err)=>{
					this.setState({loading:false}, () => {
						if (err.response &&  err.response.data) {
							NotificationManager.error(err.response.data.message)
						}
						else {
							NotificationManager.error('unknown error!')
						}	
					})	
				})	
			})
	}
	
	ifParse(val){
		if (val) {
			return val
			//console.log('VAL:',val)
		} else {
			return []
		}
	}

	dateTimeSave(item, e, t) {
		
		if (t === "date" ) {
			let date
			let dt1 = new Date(e.target.value)
			if (this.state.data[item.title]) {
				let dt0 = this.state.data[item.title].split(' ')[1]
				date = dt1.getFullYear()  + "-" + (dt1.getMonth()+1) + "-" + dt1.getDate() + " " + dt0; 	
				
			} else {
				date = dt1.getFullYear()  + "-" + (dt1.getMonth()+1) + "-" + dt1.getDate() + " 00:00"; 	
			}
			if (dt1.getFullYear())
				this.changeData(item.col, item.title, date, item)	
		} else {
			
			let time
			if (this.state.data[item.title]) {
				let dt0 = this.state.data[item.title].split(' ')[0];
				let dt = dt0.split('.')
				
				if (dt.length>1)
					time = dt[2] + "-" + dt[1] + "-" + dt[0] + " " + e.hour + ":" + e.minute 	
				else 
					time = dt0 + " " + e.hour + ":" + e.minute 	

			} else {
				let dt1 = new Date()
				time = dt1.getFullYear()  + "-" + dt1.getMonth() + "-" + dt1.getDate() + " " + e.hour + ":" + e.minute 

			}
			this.changeData(item.col, item.title, time, item)
		}

		
		//
	}
	
	
	render() {
		return (
			<div style = {{margin:"2%"}} className = {this.state.classname || "card"}>
				<PreLoader on = {this.state.loading} />
				<h1>{this.state.title}</h1>

				{this.state.config.filter((item) => item.visible && this.visibleCondition(this.state.data,item.visible_condition)).map((item) => {
					return (<div style = {{margin:"1%"}} key = {item.title + item.t}>
					
						<div id = "table">
						{(() => {switch(item.type) {
							case "select":
							  return (<div key = {item.title}>
										<b>{item.title}</b>
										<Select
											style = {{width:item.width}}
											className = {item.classname}
											onFocus = {() => this.getSelectData(item.col)}
											value = {(item.selectdata || []).find(option => option.value === this.state.data[item.title])}
											onChange = {(e) => this.changeData(item.col, item.title, e.value, item)}
											options = {item.selectdata} />
											</div>)
							case "typehead":
							  return (<div key = {item.title}>
										<b>{item.title}</b>
										<Select
											style = {{width:item.width}}
											className = {item.classname}
											onKeyDown = {(e) => {
													console.log(e.keyCode, e.target.value)
													if (e.keyCode === 13)
														this.getTypeheadData(item, e.target.value)
												}}
											value = {(item.selectdata || []).find(option => option.value === this.state.data[item.title])}
											onChange = {(e) => this.changeData(item.col, item.title, e.value, item)}
											options = {item.selectdata} />
											</div>)
							case "autocomplete":
							  return (<div key = {item.title}>
										<b>{item.title}</b>
										<Select
											style = {{width:item.width}}
											className = {item.classname}
											onKeyDown = {(e) => this.getAutocomData(item, e.target.value)}
											value = {{value:this.state.data[item.title],label:this.state.data[item.title]}}
											onChange = {(e) => this.changeData(item.col, item.title, e.value, item)}
											options = {item.selectdata} />
											</div>)				
							case "checkbox":
							  return (<div className = "form-control" key = {item.title}>
										<div><b>{item.title}</b></div>
										<input placeholder = {item.title}
										   style = {{width:item.width}}	
										   className = {item.classname || "form-control"}
										   checked = {this.state.data[item.title]} 
										   onChange = {(e) => this.changeData(item.col, item.title, e.target.checked, item)} 
										   type = {item.type}
											/>
									</div>)	
							case "multiselect":
							  return (<div key = {item.title + "multi"}>
										<b>{item.title}</b>
										<Select
											isMulti
											style = {{width:item.width}}
											className = {item.classname}
											onFocus = {() => this.getMultiSelectData(item,'')}
											onKeyDown = {(e) => this.getMultiSelectData(item,e.target.value)}
											value = {this.multiSelectValue(this.state.data[item.title], item.title)}
											onChange = {(e) => this.changeData(item.col, item.title, e, item)}
											options = {item.selectdata} />
											</div>)	
							case "multitypehead":
							  return (<div key = {item.title}>
										<b>{item.title}</b>
										<Select
											isMulti
											style = {{width:item.width}}
											className = {item.classname}
											onKeyDown = {(e) => {
													console.log(e.keyCode, e.target.value)
													if (e.keyCode === 13)
														this.getMultiTypeHeadData(item,e.target.value)
												}}	
									
											value = {this.multiSelectValue(this.state.data[item.title], item.title)}
											onChange = {(e) => this.changeData(item.col, item.title, e, item)}
											options = {item.selectdata} />
											</div>)											
							case "label":
							  return (<div className = {item.classname} key = {item.title}>
											<b>{item.title}:</b>
											<label
												
											    style = {{width:item.width, margin:5}}
												>{this.state.data[item.title]}</label>
										</div>)	
							case "time":
							  return (<div key = {item.title}>
										<div className="input-group">
											<b><span className="basic-url">{item.title}</span>
											</b>
										</div>
										    <TimePickerWrapper
												minuteStep = "1"
												time =  {this.state.data[item.title]}
												onTimeChange = {(e) => {let time = e.hour + ":" + e.minute; this.changeData(item.col, item.title, time, item)}}
											/>
									  </div>)
							case "color":
								return (
								<div key = {item.title}>
									<b><span className="basic-url">{item.title}</span></b>
									<div style = {{width:50, height:50, backgroundColor:this.state.data[item.title]}}>
									</div>
								</div>
							)
							case "colorpicker":
							  return (<div key = {item.title}>
											<b><span className="basic-url">{item.title}</span>
											</b>

											 <SketchPicker
												color={ this.state.data[item.title] }
												onChangeComplete={(e) =>  this.changeData(item.col, item.title, e.hex, item) }
											 />
									  </div>)		  
							case "datetime":
							  return (<div key = {item.title}>
										<div className="input-group">
											<b><span className="basic-url">{item.title}</span>
											</b>
										</div><input type = "date" 
											onBlur = {(e) => this.dateTimeSave(item,e,"date")}
											className = "form-control"
											defaultValue =  {(this.state.data[item.title])?
													(this.state.data[item.title].replace(/\./g,'-').split(' ')[0]).split("-").reverse().join("-")
													:
													null
												} />
										    <TimePickerWrapper
												minuteStep = "1"
												time =  {(this.state.data[item.title])? 
													this.state.data[item.title].split(' ')[1]
													:
													null
													}
												onTimeChange = {(e) => this.dateTimeSave(item,e,"time")}
											/>
									  </div>)									  
							case "file":
							  return (	<div key = {item.title}>
											<div className="input-group">
												<b><span className="basic-url">{item.title}</span>
												</b>
											</div>
											<input 
												className = {item.classname || "form-control"}
												onBlur = {(e) => this.saveFile(e,item)}
												style = {{width:item.width}} 
												type = "file"
												placeholder = {item.title} />
											<ul>{this.ifParse(this.state.data[item.title]).map((x) => 
												<li key = {x.uri}>{x.label || x.filename}
													<input value = {x.label} onChange = {(e) => this.imageSize(item,x.uri,"label",e.target.value)} placeholder = 'label' />
													<a target="_blank" rel="noopener noreferrer" href = {Api + x.uri}>
														<i className = "fa fa-link"></i>
													</a>
													<button className = "btn btn-light" onClick = {() => this.delFile(item,x.uri)}>
														<i className = "fa fa-times"></i>
													</button>
												</li>)}</ul>
										</div>)	
							case "files":
							  return (	<div key = {item.title}>
											<div><b>{item.title}</b></div>
											<input 
												multiple 
												className = {item.classname || "form-control"}
												onBlur = {(e) => this.saveFile(e,item)}
												style = {{width:item.width}} 
												type = "file"
												placeholder = {item.title} />
											<ul>{this.ifParse(this.state.data[item.title]).map((x) => <li key = {x.uri}><div className = "row"><div className = "col-md-3">{x.label || x.filename}</div>  
														<div className = "col-md-1">
															<input value = {x.label} onChange = {(e) => this.imageSize(item,x.uri,"label",e.target.value)} placeholder = 'label' />
															<a target="_blank" rel="noopener noreferrer" href = {Api + x.uri }>
																<i className = "fa fa-link"></i>
															</a>
															<button className = "btn btn-light" onClick = {() => this.delFile(item,x.uri)}>
																<i className = "fa fa-times"></i>
															</button>
															
														</div>
													</div>
												</li>)}
											</ul>
										</div>)	
							case "image":
							  return (	<div  key = {item.title}>
											<div className="input-group">
												<b><span className="basic-url">{item.title}</span>
												</b>
											</div>
											
											<input 
												accept="image/*"
												className = {item.classname || "form-control"}
												onBlur = {(e) => this.saveFile(e,item)}
												style = {{width:item.width}} 
												type = "file"
												placeholder = {item.title} />
												<ul>{this.ifParse(this.state.data[item.title]).map((x) => 
													<li key = {x.uri}>{x.filename}
														<input value = {x.height} onChange = {(e) => this.imageSize(item,x.uri,"height",e.target.value)} placeholder = 'height' />
														<input value = {x.width} onChange = {(e) => this.imageSize(item,x.uri,"width",e.target.value)} placeholder = 'width' />
														<a target="_blank" rel="noopener noreferrer" href = {Api + x.uri }>
															<i className = "fa fa-link"></i>
														</a>
														<button className = "btn btn-light" onClick = {() => this.delFile(item,x.uri)}>
															<i className = "fa fa-times"></i>
														</button>
													</li>)}
												</ul>
												
												<Gallery
													enableLightbox={true}
													enableImageSelection={false}
													images={this.ifParse(this.state.data[item.title])} />
												
										</div>)	
							case "images":
							  return (	<div key = {item.title}>
											<div className="input-group">
												<b><span className="basic-url">{item.title}</span>
												</b>
											</div>
											
											<input
												accept="image/*"
												multiple 
												className = {item.classname || "form-control"}
												onBlur = {(e) => this.saveFile(e,item)}
												style = {{width:item.width}} 
												type = "file"
												placeholder = {item.title} />
											
												<ul>{this.ifParse(this.state.data[item.title]).map((x) => 
													<li key = {x.uri}>{x.filename}
														<input value = {x.height} onChange = {(e) => this.imageSize(item,x.uri,"height",e.target.value)} placeholder = 'height' />
														<input value = {x.width} onChange = {(e) => this.imageSize(item,x.uri,"width",e.target.value)} placeholder = 'width' />
														<a target="_blank" rel="noopener noreferrer" href = {Api + x.uri }>
															<i className = "fa fa-link"></i>
														</a>
														<button className = "btn btn-light" onClick = {() => this.delFile(item,x.uri)}>
															<i className = "fa fa-times"></i>
														</button>
													</li>)}
												</ul>
												
												<Gallery
													enableLightbox={true}
													enableImageSelection={false}
													images={this.ifParse(this.state.data[item.title])} />
												
										</div>)		
							case "gallery":
							  return (	<div key = {item.title}>
											<div className="input-group">
												<b><span className="basic-url">{item.title}</span>
												</b>
											</div>
												
												<Gallery
													enableLightbox={true}
													enableImageSelection={false}
													images={this.ifParse(this.state.data[item.title])} />
												
										</div>)		
							case "filelist":
							  return (	<div key = {item.title}>
											<div className="input-group">
												<b><span className="basic-url">{item.title}</span>
												</b>
											</div>
												
											<ul>{this.ifParse(this.state.data[item.title]).map((x) => <li key = {x.uri}><div className = "row"><div className = "col-md-3">{x.label || x.filename}</div>  
														<div className = "col-md-1">
															<a target="_blank" rel="noopener noreferrer" href = {Api + x.uri }>
																<i className = "fa fa-link"></i>
															</a>
														</div>
													</div>
												</li>)}
											</ul>
										</div>)											
							default:
								return ( 
											<div key = {item.title}>
												<div><b>{item.title}</b></div>
												<input 
													value = {this.state.data[item.title]} 
													onChange = {(e) => {
														let data = this.state.data
														data[item.title] = e.target.value
														this.setState({data:data})
													}}
												className = {item.classname || "form-control"}
												onBlur = {(e) => this.changeData(item.col, item.title, e.target.value, item)} 
												style = {{width:item.width}} type = {item.type} 
												placeholder = {item.title} />
											</div>
												
						)}	
						})()}
						</div>
					
					</div>)
					}
				)}	
				<div className = "col-md-12 row d-print-none">

					{this.state.acts.filter((x) => !x.isforevery).map((itm)=>(<div>	
						{(this.visibleCondition(this.state.data,itm.act_visible_condition))? 
							<div>
							{(() =>{switch (itm.type) {
								case "Link": return (
									<Link key = {itm.title} title = {itm.title}
										className = {itm.classname || "btn"}
										style={{ textDecoration: 'none', color:"black" }} 
										to = {itm.act + this.queryBuilder2(itm)} >
										{itm.title + " "}
										<i className={itm.icon}></i>
									</Link>)
								case "Delete": return (
									<button className = {itm.classname || "btn"} key = {itm.title} >
										<i className={itm.icon}></i>
									</button>
								)
								case "API": return (
									<button className = {itm.classname || "btn"} key = {itm.title} onClick = {() => this.callAPI(itm)} >
										<i className={itm.icon}>{itm.title}</i>
									</button>
								)
								case "Print Data": return (
									<Link key = {itm.title} title = {itm.title}
										className = {itm.classname || "btn"}
										style={{ textDecoration: 'none', color:"black" }} 
										to = {'/getoneprint/' + this.state.path + this.state.search} >
										{(!itm.icon || itm.icon.length===0)?
										  <i>{itm.title}</i>
										   :
										  <i className={itm.icon || itm.classname}></i>
										}
									</Link>
								)	
								default:
									return <div />
								}
							})()} </div>
							: null
				
						}	
					</div> ))
					}		
				</div>
				<NotificationContainer/>		
			</div>	
		);
	}
}

export default GetOne
